body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: flex;
  justify-content: center;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.product_container {
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: center;
    background-color:#061621;
    color: white;
}
.product_container-sub {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 16px;
    padding-left: 16px;
    padding-right: 16px;
    padding-top: 16px;
    border-top-left-radius: 35px;
    border-top-right-radius: 35px;
    background-color: aliceblue;
    color: #061621;
}

.form-container {
    background-color: aliceblue;
    color: #061621;
}
.row {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-left: 16px;
    padding-right: 16px;
    padding-top: 16px;
    border-top-left-radius: 35px;
    border-top-right-radius: 35px;
    background-color: aliceblue;
}

@media screen and (min-width: 580px){
    .row {
        display: flex;
        align-items: center;
        flex-direction: column;
        padding-left: 16px;
        padding-right: 16px;
    }
}   
.card-container {
    display: flex;
    flex-direction: column;
    width:20rem;
    align-items: center;
    border: 2px solid grey; 
    border-radius: 5px;
    padding: 10px; 
    margin-top: 10px;
    margin-left:5px;
    text-decoration: none;
    background-color: #e9fcf8;
}
a, a:hover, a:focus, a:active {
    color: inherit;
    outline: none;
    -webkit-tap-highlight-color: transparent;
}

.product-info {
    width:60%;
}

img {
    width: 200px;
    height: 150px
}
h1 {
   font-weight: 500; 
   font-size: large;
}

.container {
  display: flex;
  flex-direction: column;
  text-align: center;
  justify-content: center;
  background-color:#061621;
}

header {
  color: white;
  padding:16px; 
}
